export default {
  name: "about",
  layout: "main",
  data() {
    return {
      name: "",
      subject: "",
      email: "",
      company: "",
      phone: "",
      text: ""
    }
  },
  methods: {
    sendMail() {

      this.$axios.$post('api//email/sendEmail', {
        name: this.name,
        company: this.company,
        phone: this.phone,
        text: this.text,
        author: this.email,
        subject: this.subject
      })

      this.name = ""
      this.subject = ""
      this.email = ""
    }
  }
}
